// 全局弹出框样式限制
.previewImgBoxWrapper {
    width: 100%;
    height: 100%;
    .previewImgBox {
        width: auto !important;
        min-width: 35%;
        max-width: 80%;
        padding-top: 24px;
        img {
            width: 100%;
            max-width: 100%;
        }

        :global {
            .ant-modal-body {
                max-height: 90vh;
                overflow-y: auto;
            }
        }
    }
}
